<template>
    <!-- 接单员待审自助订单 -->
    <div class="heigth-div percent-table">
        <el-table
            :data="abnormalOrderData"
            :height="tabHeight"
            border
            highlight-current-row
            class="th-center heigth-div">
            <el-table-column prop="buildsite" label="工程名称" align="center" min-width="50%"></el-table-column>
            <el-table-column prop="buildpartname" label="浇筑部位" align="center" min-width="21%"></el-table-column>
            <el-table-column prop="builddate" label="送货日期" align="center" min-width="20%"></el-table-column>
        </el-table>
    </div>
</template>
<script>
export default {
    data(){
        return {
            tabHeight:'50px',
            abnormalOrderData:[],
            searchForm:{
                exts:{
                    builddate_ge_date:this.recentDate(0),
                    builddate_le_date:this.recentDate(1),
                },
                status:-10
            },
        }
    },

    activated(){
        this.getlist();
    },
    
    mounted() {
        this.init();
    },
    methods: {
        init() {
            //this.initEchart();
            // this.setEchart();
            setTimeout(()=>{
                this.isorder = true;
            },20)
            this.getlist();
        },

        //获取数据
        getlist(){
            let tempData = []
            this.abnormalOrderData = [];
            this.fetch('/mktmfc/clientOrder/findAll?pageNo=1' + '&pageSize=1000' + '&orderBys=createtime_desc',this.searchForm)
                .then(result => {
                    if(result.status === 200){
                        tempData = result.data.list;
                        for(let i=0; i<tempData.length; i++){
                            tempData[i].builddate = tempData[i].builddate.substring(5,16)
                        }
                        this.abnormalOrderData = tempData;
                    }else {
                        this.$message({
                            message:this.returnIntercept(result),
                            type:'error'
                        });
                        
                    }
                })
        },

        //日期显示格式化
        format(row,column,cellValue) {
            return this.formatDate(cellValue,'ymdhM');
        },
    }
}
</script>
<style scoped lang="less" type="text/less">
    .heigth-div{
        height: 95%;
    }
</style>


